import React from "react";
import { Redirect, Route, Switch } from "react-router-dom";
import "./authorization_presents.css"

interface IAuthorizationPresents {
	img: string
	img_clsssName: string
}


const AuthorizationPresents: React.FC<IAuthorizationPresents> = (props) => {



	return (
		<>

			<div className="authorization_present">
				<div className="authorization_title">
					Маркетплейс строительной документации
				</div>
				<div className={props.img_clsssName}>
					<img src={props.img} alt="" />
				</div>
			</div>

		</>

	)
}

export default AuthorizationPresents