import { combineReducers } from "redux";
import { EnginerOrdersReducer } from "./enginerOrdersReducer";
import { LoginReducer } from "./loginReducer";
import { RegistrationReduser } from "./registrationReducer";
import { SearchOrdersReducer } from "./searchOrdersReducer";



export const Redusers = combineReducers({
	registration: RegistrationReduser,
	login: LoginReducer,
	searchOrders: SearchOrdersReducer,
	enginerOrders: EnginerOrdersReducer
})

export type TypeRedusers = ReturnType<typeof Redusers>