import React, { useEffect, useRef } from "react";
import { useDispatch, useSelector } from "react-redux";
import { bindActionCreators } from "redux";
import { TypeRedusers } from "../../../../../store/reducers/redusers";
import TextError from "../text_error/text_error";
import "./input_phone.css"
import AllFunctions from "../../../../../store/functions/Allfunctions"


const InputPhone: React.FC = () => {

	const dispatch = useDispatch()
	const ref = useRef<HTMLInputElement>(null)


	const { phone } = useSelector((state: TypeRedusers) => state.registration)
	const { OnChangePhone } = bindActionCreators(AllFunctions, dispatch)

	useEffect(() => {
		if (phone.error) {
			ref.current!.style.setProperty('--placeHolderColor', '#F61C1C')
			ref.current!.style.border = '1px solid #F61C1C'
		}
		else {
			ref.current!.style.setProperty('--placeHolderColor', '#0056d3')
			ref.current!.style.border = '1px solid #0056d3'
		}
	}, [phone.error]);

	return (
		<>
			<input
				onChange={(e) => OnChangePhone(e.target.value)}
				value={phone.value}
				ref={ref}
				id="phone"
				type="tel"
				placeholder="Ваш номер телефона" />
			{phone.error ? <TextError textError={phone.error} /> : null}
		</>
	)
}

export default InputPhone