import React, { useEffect } from 'react';
import Header from './components/header/header';
import "./App.css"
import { BrowserRouter } from 'react-router-dom';
import Website from './components/website/website';
import { useDispatch, useSelector } from 'react-redux';
import { TypeRedusers } from './store/reducers/redusers';
import AllFunctions from "./store/functions/Allfunctions"
import { bindActionCreators } from "redux";
import AuthorizationForms from './components/authorization/authorization_forms/authorization_forms';


// 3. добавить валидацию при логине - эл почта


function App() {
  const dispatch = useDispatch()
  const { CheckAuth } = bindActionCreators(AllFunctions, dispatch)

  const { loading, login } = useSelector((state: TypeRedusers) => state.login)


  useEffect(() => {
    CheckAuth(localStorage.getItem('token'))
  }, [])


  if (loading) {
    return (
      <div>
        Загрузка...
      </div>
    )
  }

  return (
    <BrowserRouter>
      <div className="App">
        <Header />
        {login ?
          <Website />
          :
          <AuthorizationForms />
        }
      </div>
    </BrowserRouter>
  )

}

export default App;
