import React, { } from "react";
import { useDispatch, useSelector } from "react-redux";
import { bindActionCreators } from "redux";
import { TypeRedusers } from "../../../../../store/reducers/redusers";
import AllFunctions from "../../../../../store/functions/Allfunctions"
import filter_icons from '../../../../../img/filter_icons.svg'
import "./filter_small.css"

const FilterSmall: React.FC = () => {

	const dispatch = useDispatch()
	const { days, userChapters } = useSelector((state: TypeRedusers) => state.searchOrders)
	const { SearchOnChangeDays, SearchOnChangeChaptersAdd } = bindActionCreators(AllFunctions, dispatch)

	let chapters = ['СОУЭ', 'ЭОМ', 'СКУД', 'КМ', 'СС', 'КЖ', 'ВК', 'АР']


	let num = `${days}`[`${days}`.length - 1]
	let text_day: string

	if (num === '1') {
		text_day = 'день'
	}
	else if (num === '2' || num === '3' || num === '4') {
		text_day = 'дня'
	}
	else {
		text_day = 'дней'
	}



	return (
		<div className='filter_small_container' >
			<img src={filter_icons} alt="" />
			<div className='filter_img_small' >
				<div className="filter_small_title" >
					<img src={filter_icons} alt="" />
					Рекомендуремые категории:
				</div>
				<div className='filter_small_content' >
					<div className="small_content_col1" >
						<p>Раздел</p>
						{chapters.map(chapter =>
							<div
								onClick={() => {
									SearchOnChangeChaptersAdd([...userChapters, chapter])
								}}
								key={chapter} >
								{chapter}
							</div>
						)}
					</div>
					<div className="small_content_col2">
						<p>Срок</p>
						<div className="small_col2" >
							<div
								onClick={() => { SearchOnChangeDays(days, 'decrement') }}>
								-
							</div>
							<div className="small_col2_day" >{days} {text_day} </div>
							<div
								onClick={() => {
									SearchOnChangeDays(days, 'increment')
								}}>
								+
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>
	)
}

export default FilterSmall


