import React, { useEffect, useRef } from "react";
import { useDispatch, useSelector } from "react-redux";
import { bindActionCreators } from "redux";
import { TypeRedusers } from "../../../../../store/reducers/redusers";
import TextError from "../text_error/text_error";
import "./input_email.css"
import AllFunctions from "../../../../../store/functions/Allfunctions"

interface IinputEmail {
	placeholder: string
	form: string
}

const InputEmail: React.FC<IinputEmail> = (props) => {

	const dispatch = useDispatch()
	const ref = useRef<HTMLInputElement>(null)


	const { email } = useSelector((state: TypeRedusers) => {
		if (props.form === "registration") {
			return state.registration
		}
		//(props.form === "login") 
		else {
			return state.login
		}
	})



	const { OnChangeEmail, logOnChangeEmail } = bindActionCreators(AllFunctions, dispatch)

	useEffect(() => {
		if (email.error) {
			ref.current!.style.setProperty('--placeHolderColor', '#F61C1C')
			ref.current!.style.border = '1px solid #F61C1C'
		}
		else {
			ref.current!.style.setProperty('--placeHolderColor', '#0056d3')
			ref.current!.style.border = '1px solid #0056d3'
		}
	}, [email.error]);


	let ChangeEmail = (value: string) => {
		if (props.form === "registration") {
			OnChangeEmail(value)
		}
		else if (props.form === "login") {
			logOnChangeEmail(value)
		}
	}

	return (
		<>
			<input
				value={email.value}
				onChange={(e) => { ChangeEmail(e.target.value) }}
				ref={ref}
				id="email"
				type="email"
				placeholder={props.placeholder} />
			{email.error ? <TextError textError={email.error} /> : null}
		</>
	)
}

export default InputEmail