import React from "react";
import "./engineer_menu.css"
import menu from '../../../../img/menu.svg'
import logo1 from '../../../../img/logo1.svg'
import cross from '../../../../img/cross.svg'
import avatar from '../../../../img/avatar.svg'
import WhiteStar from '../../../../img/WhiteStar.svg'
import BlueStar from '../../../../img/BlueStar.svg'
import { NavLink, useHistory } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { TypeRedusers } from "../../../../store/reducers/redusers";
import { bindActionCreators } from "redux";
import AllFunctions from "../../../../store/functions/Allfunctions"

const EngineerMenu: React.FC = () => {
	const history = useHistory()
	const { user } = useSelector((state: TypeRedusers) => state.login)

	const dispatch = useDispatch()
	const { Logout } = bindActionCreators(AllFunctions, dispatch)


	let reputationStar: string[] = []
	let index = 0


	if (user!.reputation) {
		for (let index = 0; index < user!.reputation; index++) {
			reputationStar.push(BlueStar)
		}

		for (let index = 0; index < 5 - user!.reputation; index++) {
			reputationStar.push(WhiteStar)
		}
	}

	return (
		<div className="menu container">
			<div
				className="menu_header"
				onClick={() => { history.goBack() }} >
				<img
					src={menu}
					alt=""
					className="menu_img" />
				<img
					src={logo1}
					alt="" />
				<img
					className="menu_img"
					src={cross}
					alt="" />
			</div>
			<div className="menu_content" >
				<div className="menu_content_title" >
					Маркетплейс строительной документации
				</div>
				<div className="menu_content_menu" >
					<div className="content_menu_list" >
						<NavLink
							to='/profile'>
							<div>Личный кабинет</div>
						</NavLink>
						<NavLink to='/search'>
							<div>Поиск заказа</div>
						</NavLink>
						<div>Приглашения</div>
						<div>Поддержка</div>
						<div>О платформе</div>
						<div>FAQ</div>
						<NavLink
							onClick={Logout}
							to='/'>
							<div>Выйти</div>
						</NavLink>
					</div>
					<div className="content_menu_data" >
						<div className="content_menu_avatar" >
							<img src={avatar} alt="" />
						</div>
						<div
							className="menu_data_name" >
							<b>{user!.name}</b>
						</div>
						<div
							className="menu_data_reputation">
							{reputationStar.map(star =>
								< img key={index += 1} src={star} alt="" />
							)}
						</div>
						<div className="menu_data_money" >
							Кошелёк: {user!.money} ₽
						</div>
						<div className="menu_data_id" >
							ID{user!.id}
						</div>
					</div>
				</div>

			</div>
			<div className="menu_footer" >
				<p>Все права защищены ©</p>
				<p>{(new Date()).getFullYear()} WillDoc.ru</p>
			</div>
		</div>
	)
}

export default EngineerMenu