import React from "react";
import { useDispatch, useSelector } from "react-redux"
import { bindActionCreators } from "redux"
import { TypeRedusers } from "../../../../../store/reducers/redusers"
import AllFunctions from "../../../../../store/functions/Allfunctions"
import magnifier from '../../../../../img/magnifier.svg'
import exit_blue from '../../../../../img/exit_blue.svg'
import "./search_input.css"


const SearchInput: React.FC = () => {
	const dispatch = useDispatch()
	const state = useSelector((state: TypeRedusers) => state.searchOrders)
	const userState = useSelector((state: TypeRedusers) => state.login)

	const { SearchOnChangeINPUT, SearchOrders } = bindActionCreators(AllFunctions, dispatch)



	return (
		<div className="search_input" >
			<input
				value={state.inputText}
				onChange={(e) => { SearchOnChangeINPUT(e.target.value) }}
				onKeyDown={(event) => {
					if (event.key === 'Enter') {
						SearchOrders(
							userState.user!.id,
							userState.user!.name,
							userState.user!.rol,
							userState.token,
							state
						)
					}
				}}
				placeholder="Введите код шифра или наименование подрядчика"
				type="text" />
			{
				state.inputText ?
					<img
						onClick={() => { SearchOnChangeINPUT('') }}
						src={exit_blue} alt="" />
					:
					<img src={magnifier} alt="" />
			}
		</div>
	)
}

export default SearchInput
