// тут будут действия для принятия заказа

export interface ISearchOrder {
	id: string
	title: string
	group: string
	hot: boolean
	days: number
	ready: number
	details: string
}


export interface ISearchOrders {
	inputText: string
	userChapters: string[]
	days: number
	orders: ISearchOrder[]
	title_text: string
}



export enum SearchOrdersActionTypes {
	OnChange_INPUT = 'OnChange_INPUT',
	OnChange_Days = 'OnChange_Days',
	OnChange_Chapters = 'OnChange_Chapters',
	SearchOrders = 'SearchOrders'
}

interface I_SearchOrders_Action {
	type: SearchOrdersActionTypes.SearchOrders
	payload: {
		orders: ISearchOrder[]
		title_text: string
	}
}

interface I_OnChange_INPUT_Action {
	type: SearchOrdersActionTypes.OnChange_INPUT
	payload: string
}

interface I_OnChange_Days_Action {
	type: SearchOrdersActionTypes.OnChange_Days
	payload: number
}

interface I_OnChange_Chapters_Action {
	type: SearchOrdersActionTypes.OnChange_Chapters
	payload: string[]
}



export type SearchOrdersAction =
	I_OnChange_INPUT_Action |
	I_OnChange_Days_Action |
	I_OnChange_Chapters_Action |
	I_SearchOrders_Action