const Contractor: React.FC = () => {
	return (
		<>
			<Switch>
				<Route path='/profile'>
					<ContractorProfile />
				</Route>
				<Route path='/orders'>
					<ContractorOrders />
				</Route>
				<Redirect to='/profile' />
			</Switch>
		</>
	)
}

export default Contractor
