import React, { useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { TypeRedusers } from "../../../../store/reducers/redusers";
import "./contractor_profile.css"
import avatar from '../../../../img/avatar.svg'
import { NavLink } from "react-router-dom";
import { bindActionCreators } from "redux";
import AllFunctions from "../../../../store/functions/Allfunctions"

const ContractorProfile: React.FC = () => {

	const dispatch = useDispatch()
	const { Logout } = bindActionCreators(AllFunctions, dispatch)

	const { user } = useSelector((state: TypeRedusers) => state.login)
	const [click, setClick] = useState(false)

	return (
		<div className='profile_container container'>
			<div className="profile back_white text_blue" >
				<div className="profile_header" >
					<div className="profile_data" >
						<div className="profile_avatar">
							<img src={avatar} alt="" />
						</div>
						<div>
							<div>
								<b>
									{user!.name}
								</b>
							</div>
							<div>
								Кошелёк: {user!.money}р
							</div>
						</div>
					</div>
				</div>
				<div className="profile_menu" >
					<div
						onClick={() => {
							setClick(!click)
						}}>
						{click ? 'Заказы в работе -' : 'Заказы в работе +'}
					</div>
					{click ?
						<>
							<NavLink to="/orders">
								<div className="order_list" >Согласованные заказы</div>
							</NavLink>
							<NavLink to="/orders">
								<div className="order_list">Несогласованные заказы</div>
							</NavLink>
							<NavLink to="/orders">
								<div className="order_list">Заказы в ожидании</div>
							</NavLink>
							<NavLink to="/orders">
								<div className="order_list">Закрытые заказы</div>
							</NavLink>
						</>
						:
						null
					}
					<div>Редактировать информацию</div>
					<NavLink
						onClick={Logout}
						to='/'>
						<div>Выйти</div>
					</NavLink>
				</div>
			</div>
		</div>
	)
}

export default ContractorProfile