import { ISearchOrder, ISearchOrders, SearchOrdersAction, SearchOrdersActionTypes } from "../../types/searchOrders"



export const SearchOnChangeINPUT = (value: string): SearchOrdersAction => {
	return {
		type: SearchOrdersActionTypes.OnChange_INPUT,
		payload: value
	}
}


export const SearchOnChangeDays = (value: number, operant: string): SearchOrdersAction => {

	if (operant === 'increment') {
		value = value + 1
	}
	else {
		if (value <= 0) {
			value = 0
		}
		else {
			value = value - 1
		}
	}

	return {
		type: SearchOrdersActionTypes.OnChange_Days,
		payload: value
	}
}

export const SearchOnChangeChaptersAdd = (value: string[]): SearchOrdersAction => {

	value = Array.from(new Set(value))

	return {
		type: SearchOrdersActionTypes.OnChange_Chapters,
		payload: value
	}
}

export const SearchOnChangeChaptersRemove = (value: string, value_arr: string[]): SearchOrdersAction => {

	value_arr = value_arr.filter(
		cr => value_arr.indexOf(cr)
			!== value_arr.indexOf(value))


	return {
		type: SearchOrdersActionTypes.OnChange_Chapters,
		payload: value_arr
	}
}

export const SearchOrders = (userId: string, userName: string, userRol: string, userToken: string, state: ISearchOrders): SearchOrdersAction => {


	// указать тригер поиска данных
	// на сервер отправляем объект:
	// на сервере сопоставляем токен и данные пользователя, а потом проверяем
	// полномочия по роли и выгружаем данные из базы по запросу Orders

	let ForServerObj = {
		user: {
			id: userId,
			name: userName,
			rol: userRol,
			token: userToken
		},
		orders: {
			days: state.days,
			input: state.inputText,
			chapters: state.userChapters
		}
	}

	// с сервера приходит массив заказов типа:
	let OrdersAtServer: ISearchOrder[] = [
		{
			days: 3,
			details: 'Полное описание заказа',
			group: 'СОУЭ',
			hot: true,
			id: 'HHH111111',
			ready: 50,
			title: 'Поверка'
		},
		{
			days: 1,
			details: 'Заказа описание полное',
			group: 'СС',
			hot: false,
			id: 'БББ222222',
			ready: 10,
			title: 'Чертеж'
		}
	]

	return {
		type: SearchOrdersActionTypes.SearchOrders,
		payload: {
			orders: OrdersAtServer,
			title_text: `Проектов найдено ${OrdersAtServer.length}`
		}
	}
}