import React from "react";
import { Redirect, Route, Switch } from "react-router-dom";
import "./authorization_forms.css"
import LoginForm from "./login_form/login_form";
import present1 from '../../../img/present1.png'
import present2 from '../../../img/present2.png'

import RegistrationForm from "./registration_form/registration_form";
import AuthorizationPresents from "../authorization_presents/authorization_presents";

const AuthorizationForms: React.FC = () => {
	return (
		<div className="authorization_main container">
			<div className="authorization">
				<Switch>
					<Route path='/' exact>
						<AuthorizationPresents
							img={present1}
							img_clsssName='authorization_img_1' />
						<RegistrationForm />
					</Route>
					<Route path='/login'>
						<AuthorizationPresents
							img={present2}
							img_clsssName='authorization_img_2' />
						<LoginForm />
					</Route>
					<Redirect to="/" />
				</Switch>
			</div>
		</div>

	)
}

export default AuthorizationForms