import React, { useState } from "react";
import "./engineer_profile.css"
import avatar from '../../../../img/avatar.svg'
import { TypeRedusers } from "../../../../store/reducers/redusers";
import { useDispatch, useSelector } from "react-redux";
import { NavLink } from "react-router-dom";
import { bindActionCreators } from "redux";
import AllFunctions from "../../../../store/functions/Allfunctions"

const EngineerProfile: React.FC = () => {

	const dispatch = useDispatch()
	const { EnginerAgreedOrders, EnginerNotAgreedOrders, EnginerReadyOrders, EnginerWaitOrders, TransactionUpdate } = bindActionCreators(AllFunctions, dispatch)

	const { user, token } = useSelector((state: TypeRedusers) => state.login)
	const [click, setClick] = useState(false)

	return (
		<div className='profile_container container'>
			<div className="profile back_white text_blue" >
				<div className="profile_header" >
					<div className="profile_data" >
						<div className="profile_avatar">
							<img src={avatar} alt="" />
						</div>
						<div>
							<div><b>{user!.name}</b></div>
							<div>Кошелёк: {user!.money}р</div>
							<div>Образование: {user!.education}</div>
							<div><b>Опыт работы {user!.experience}</b></div>
						</div>
					</div>
				</div>
				<div className="profile_menu" >
					<div
						onClick={() => {
							setClick(!click)
						}}>
						{click ? 'Заказы в работе -' : 'Заказы в работе +'}
					</div>

					{click ?
						<>
							<NavLink
								onClick={() => EnginerAgreedOrders(user!.id, user!.name, user!.rol, token)}
								to="/orders">
								<div className="order_list" >Согласованные заказы</div>
							</NavLink>

							<NavLink
								onClick={() => EnginerNotAgreedOrders(user!.id, user!.name, user!.rol, token)}
								to="/orders">
								<div className="order_list">Несогласованные заказы</div>
							</NavLink>
							<NavLink
								onClick={() => EnginerWaitOrders(user!.id, user!.name, user!.rol, token)}
								to="/orders">
								<div className="order_list">Заказы в ожидании</div>
							</NavLink>
						</>
						:
						null
					}

					<NavLink
						onClick={() => TransactionUpdate(user!.email, token)}
						to='/money'>
						<div>Вывести средства</div>
					</NavLink>

					<NavLink
						onClick={() => EnginerReadyOrders(user!.id, user!.name, user!.rol, token)}
						to='/orders'>
						<div>Выполненные работы</div>
					</NavLink>
					<div>Редактировать информацию</div>
				</div>
			</div>
		</div>
	)
}

export default EngineerProfile