import React, { useEffect, useRef } from "react";
import { useDispatch, useSelector } from "react-redux";
import { bindActionCreators } from "redux";
import TextError from "../text_error/text_error";
import "./input_name.css"
import AllFunctions from "../../../../../store/functions/Allfunctions"
import { TypeRedusers } from "../../../../../store/reducers/redusers";


const InputName: React.FC = () => {


	const dispatch = useDispatch()
	const ref = useRef<HTMLInputElement>(null)

	const { name } = useSelector((state: TypeRedusers) => state.registration)
	const { OnChangeName } = bindActionCreators(AllFunctions, dispatch)


	useEffect(() => {
		if (name.error) {
			ref.current!.style.setProperty('--placeHolderColor', '#F61C1C')
			ref.current!.style.border = '1px solid #F61C1C'
		}
		else {
			ref.current!.style.setProperty('--placeHolderColor', '#0056d3')
			ref.current!.style.border = '1px solid #0056d3'
		}
	}, [name.error]);

	return (
		<>
			<input
				onChange={
					(e) => OnChangeName(e.target.value)
				}
				value={name.value}
				ref={ref}
				id="name"
				type="text"
				placeholder="Ваше имя" />
			{name.error ? <TextError textError={name.error} /> : null}
		</>
	)
}

export default InputName
