import React from "react";
import { useSelector } from "react-redux";
import { TypeRedusers } from "../../../../store/reducers/redusers";
import "./engineer_orders.css"
import { Redirect } from "react-router-dom";
import EngineerOrder from "../engineer_order/engineer_order";

const EngineerOrders: React.FC = () => {

	const { orders, loading } = useSelector((state: TypeRedusers) => state.enginerOrders)

	if (orders.length === 0) {
		return (
			<Redirect to="/profile" />
		)
	}
	return (
		// временные заказы Переделать как будет пример заказов
		< div className="engineer_orders " >
			<EngineerOrder orders={orders} />
		</div >
	)
}

export default EngineerOrders