import { EngineerOrdersAction, EnginerOrdersActionTypes, IEnginerOrders } from "../../types/engineerOrders"


const initialState: IEnginerOrders = {
	orders: [],
	loading: true
}

export const EnginerOrdersReducer = (
	state = initialState,
	action: EngineerOrdersAction
): IEnginerOrders => {
	switch (action.type) {
		case EnginerOrdersActionTypes.EnginerOrders:
			return {
				loading: false,
				orders: action.payload
			}
		default:
			return state
	}
}