import React from "react";
import { Redirect, Route, Switch } from "react-router-dom";
import "./engineer.css"
import EngineerMenu from "./engineer_menu/engineer_menu";
import EngineerMoney from "./engineer_money/engineer_money";
import EngineerOrders from "./engineer_orders/engineer_orders";
import EngineerProfile from "./engineer_profile/engineer_profile";
import EngineerSearch from "./engineer_search/engineer_search";



const Engineer: React.FC = () => {
	return (
		<>
			<Switch>
				<Route path='/profile'>
					<EngineerProfile />
				</Route>
				<Route path='/menu'>
					<EngineerMenu />
				</Route>
				<Route path='/search'>
					<EngineerSearch />
				</Route>
				<Route path='/orders'>
					<EngineerOrders />
				</Route>
				<Route path='/money'>
					<EngineerMoney />
				</Route>
				<Redirect to='/profile' />
			</Switch>
		</>
	)
}

export default Engineer
