import React from "react";
import "./engineer_order.css"
import hot from '../../../../img/hot.svg'
import present1 from '../../../../img/present1.png'
import { ISearchOrder } from "../../../../types/searchOrders";
import { IEnginerOrder } from "../../../../types/engineerOrders";

interface ILEngineerOrder {
	orders: ISearchOrder[] | IEnginerOrder[]
}

const EngineerOrder: React.FC<ILEngineerOrder> = (props) => {
	return (
		<div className="search_orders container" >
			{
				props.orders.length === 0 ?
					<div>
						<img
							className="search_orders_present"
							src={present1} alt="" />
					</div>
					:
					<>
						{
							props.orders.map(order =>
								<div key={order.id} className="search_order" >
									<div>
										<span>
											Шифр проекта:
										</span>
										<div>
											{order.id}
										</div>
									</div>
									<div>
										<span>
											Раздел:
										</span>
										<div>
											{order.group}
										</div>
									</div>
									<div>
										<span>
											Срок:
											{
												order.hot ?
													<div className="hot_img" >
														<img src={hot} alt="Срочный проект" />
													</div>
													:
													null
											}
										</span>
										<div>
											{order.days} дня
										</div>
									</div>
									<div>
										<span>
											Описание проекта:
										</span>
										<div className="smallDescription">
											{order.details}
										</div>
									</div>
									<div className="orderDetails" >
										<div>
											<a href="#">Подробнее...</a>
										</div>
									</div>
								</div>
							)
						}
					</>
			}
		</div>
	)
}

export default EngineerOrder