import { IUser } from "./user";

interface IinputState {
	value: string
	error: string | null
}

interface IcheckboxState {
	chAction: boolean
	error: null
}

export interface ILoginFormState {
	email: IinputState
	password: IinputState
	checkbox: IcheckboxState
	login: boolean
	error: string | null
	user: IUser | null
	loading: boolean
	token: string
}

export enum LoginFormActionTypes {
	OnChange_EMAIL = "logOnChange_EMAIL",
	OnChange_PASS = "logOnChange_PASS",
	OnClick_CHECKBOX = "logOnClick_Checkbox",
	Login = "Login",
	CheckAuth = "CheckAuth",
	Logout = "Logout",
	Loading = "Loading",
	Transaction = "Transaction",
	TransactionUpdate = "TransactionUpdate"
}

interface I_OnChange_EMAIL_Action {
	type: LoginFormActionTypes.OnChange_EMAIL
	payload: string
}

interface I_OnChange_PASS_Action {
	type: LoginFormActionTypes.OnChange_PASS
	payload: string
}

interface I_OnClick_CHECKBOX_Action {
	type: LoginFormActionTypes.OnClick_CHECKBOX
	payload: boolean
}

interface I_CheckAuth_Action {
	type: LoginFormActionTypes.CheckAuth
	payload: {
		login: boolean
		user: IUser | null
		token: string
	}
}

interface I_Login_Action {
	type: LoginFormActionTypes.Login
	payload: {
		login: boolean
		error: string | null
		user: IUser | null
		token: string
	}
}

interface I_Logout_Action {
	type: LoginFormActionTypes.Logout
}


interface I_Loading_Action {
	type: LoginFormActionTypes.Loading
	payload: boolean
}


interface I_Transaction_Action {
	type: LoginFormActionTypes.Transaction
	payload: {
		transaction: boolean
		money: number
	}
}

export type LoginFormAction =
	I_OnChange_EMAIL_Action |
	I_OnChange_PASS_Action |
	I_OnClick_CHECKBOX_Action |
	I_Login_Action |
	I_CheckAuth_Action |
	I_Logout_Action |
	I_Loading_Action |
	I_Transaction_Action