import React, { useRef, useState } from "react";
import { useDispatch, useSelector } from "react-redux";
import { bindActionCreators } from "redux";
import arrowDown from '../../../../../img/ArrowDown.svg'
import { TypeRedusers } from "../../../../../store/reducers/redusers";
import "./rols.css"
import AllFunctions from "../../../../../store/functions/Allfunctions"

const Rols: React.FC = () => {

	const dispatch = useDispatch()
	const rolsRef = useRef<HTMLDivElement>(null)

	const { rols } = useSelector((state: TypeRedusers) => state.registration)
	const { OnClickRol } = bindActionCreators(AllFunctions, dispatch)


	const [trige, UseTrige] = useState(true)




	let RolsArr = () => {
		if (trige) {
			rolsRef.current!.style.height = 'auto'
			UseTrige(false)
		}
		else {
			rolsRef.current!.style.height = '60px'
			UseTrige(true)
		}
	}

	return (
		<>
			<div className="rols_container">
				<div
					ref={rolsRef}
					className="rols">
					<div
						className="rols_list">
						<img src={arrowDown} alt="" />

						{rols.map(rol =>
							<div
								onClick={() => {
									RolsArr()
									OnClickRol(rol, rols)
								}}
								key={rols.indexOf(rol)} >
								{rol}
							</div>
						)}
					</div>
				</div>
			</div>
		</>
	)
}

export default Rols