import React from "react";
import { useSelector } from "react-redux";
import { TypeRedusers } from "../../store/reducers/redusers";
import Contractor from "./contractor/contractor";
import Customer from "./customer/customer";
import Engineer from "./engineer/engineer";
import "./website.css"


const Website: React.FC = () => {
	const { user } = useSelector((state: TypeRedusers) => state.login)

	

	if (user!.rol === "Я инженер") {
		return (
			<Engineer />
		)
	}
	else if (user!.rol === "Я подрядчик") {
		return (
			<Contractor />
		)
	}

	else if (user!.rol === "Я заказчик") {
		return (
			<Customer />
		)
	}
	else {
		return (
			<>
				<h1>
					Ошибка в персональных данных.
				</h1>
				<h1>
					Обратитесь к администратору.
				</h1>
			</>
		)
	}
}

export default Website