import React, { useEffect, useRef } from "react";
import { useDispatch, useSelector } from "react-redux";
import { bindActionCreators } from "redux";
import checkboxV from "../../../../../img/V.svg"
import { TypeRedusers } from "../../../../../store/reducers/redusers";
import TextError from "../text_error/text_error";
import "./checkbox.css"
import AllFunctions from "../../../../../store/functions/Allfunctions"


interface ICheckbox {
	label_text: string
	form: string
}

const Checkbox: React.FC<ICheckbox> = (props) => {


	const dispatch = useDispatch()
	const ref = useRef<HTMLDivElement>(null)


	const { checkbox } = useSelector((state: TypeRedusers) => {
		if (props.form === "registration") {
			return state.registration
		}
		//(props.form === "login") 
		else {
			return state.login
		}
	})
	const { OnClickCheckbox, logOnClickCheckbox } = bindActionCreators(AllFunctions, dispatch)


	useEffect(() => {
		if (checkbox.error) {
			ref.current!.style.border = '1px solid #F61C1C'
		}
		else {
			ref.current!.style.border = '1px solid #0056d3'
		}

		if (checkbox.chAction) {
			ref.current!.style.backgroundColor = "#0056D3"
		}
		else {
			ref.current!.style.backgroundColor = "white"
		}
	}, [checkbox.error, checkbox.chAction]);



	let ClickCheckbox = () => {
		if (props.form === "registration") {
			OnClickCheckbox(!checkbox.chAction)
		}
		else if (props.form === "login") {
			logOnClickCheckbox(!checkbox.chAction)
		}
	}


	return (
		<>
			<div
				onClick={ClickCheckbox}
				className="form_box_checkbox">
				<div
					ref={ref} className="checkbox">
					<img
						src={checkboxV}
						alt="" />
				</div>
				<div
					className="label">
					{props.label_text}
				</div>
			</div>
			{checkbox.error ? <TextError textError={checkbox.error} /> : null}
		</>
	)
}

export default Checkbox