import React, { useEffect, useRef } from "react";
import { useDispatch, useSelector } from "react-redux";
import { bindActionCreators } from "redux";
import { TypeRedusers } from "../../../../../store/reducers/redusers";
import AllFunctions from "../../../../../store/functions/Allfunctions"
import TextError from "../text_error/text_error";
import "./input_password.css"

interface IinputPassword {
	placeholder: string
	form: string
}

const InputPassword: React.FC<IinputPassword> = (props) => {

	const dispatch = useDispatch()
	const ref = useRef<HTMLInputElement>(null)


	const { password } = useSelector((state: TypeRedusers) => {
		if (props.form === "registration") {
			return state.registration
		}
		//(props.form === "login") 
		else {
			return state.login
		}
	})
	const { OnChangePass, logOnChangePass } = bindActionCreators(AllFunctions, dispatch)

	useEffect(() => {
		if (password.error) {
			ref.current!.style.setProperty('--placeHolderColor', '#F61C1C')
			ref.current!.style.border = '1px solid #F61C1C'
		}
		else {
			ref.current!.style.setProperty('--placeHolderColor', '#0056d3')
			ref.current!.style.border = '1px solid #0056d3'
		}
	}, [password.error]);


	let ChangePass = (value: string) => {
		if (props.form === "registration") {
			OnChangePass(value)
		}
		else if (props.form === "login") {
			logOnChangePass(value)
		}
	}

	return (
		<>
			<input
				onChange={(e) => { ChangePass(e.target.value) }}
				value={password.value}
				ref={ref}
				id="password"
				type="text"
				placeholder={props.placeholder} />
			{password.error ? <TextError textError={password.error} /> : null}
		</>
	)
}

export default InputPassword
