import { Dispatch } from "react"
import { ILoginFormState, LoginFormAction, LoginFormActionTypes } from "../../types/login"


export const logOnChangeEmail = (value: string): LoginFormAction => {
	return {
		type: LoginFormActionTypes.OnChange_EMAIL,
		payload: value
	}
}

export const logOnChangePass = (value: string): LoginFormAction => {
	return {
		type: LoginFormActionTypes.OnChange_PASS,
		payload: value
	}
}

export const logOnClickCheckbox = (value: boolean): LoginFormAction => {
	return {
		type: LoginFormActionTypes.OnClick_CHECKBOX,
		payload: value
	}
}

export const CheckAuth = (token: string | null) => {
	// мини логика проверки localStorage
	// отправлять на сервер тригер что это Login
	if (token) {
		return (dispatch: Dispatch<LoginFormAction>) => {
			try {
				///////////////////////////////////////////////////////
				if (token === 'инженер') {
					let user = {
						id: '1',
						name: 'инженер',
						email: 'инженер',
						phone: '+79111111111',
						rol: 'Я инженер',
						money: 1650,
						education: 'ЛЭТИ (2003-2008)',
						experience: 5,
						reputation: 4,
						transaction: false
					}
					dispatch({
						type: LoginFormActionTypes.CheckAuth,
						payload: {
							token: token,
							login: true,
							user: user
						}
					})
				}
				else if (token === 'подрядчик') {
					let user = {
						id: '1',
						name: 'подрядчик',
						email: 'подрядчик',
						phone: '+79111111111',
						rol: 'Я подрядчик',
						money: 1650,
						education: 'ЛЭТИ (2003-2008)',
						experience: 5,
						reputation: 4,
						transaction: false
					}
					dispatch({
						type: LoginFormActionTypes.CheckAuth,
						payload: {
							token: token,
							login: true,
							user: user
						}
					})
				}
				else if (token === 'заказчик') {
					let user = {
						id: '1',
						name: 'заказчик',
						email: 'заказчик',
						phone: '+79111111111',
						rol: 'Я заказчик',
						money: 1650,
						education: 'ЛЭТИ (2003-2008)',
						experience: 5,
						reputation: 4,
						transaction: false
					}
					dispatch({
						type: LoginFormActionTypes.CheckAuth,
						payload: {
							token: token,
							login: true,
							user: user
						}
					})
				}
				else {
					dispatch({
						type: LoginFormActionTypes.CheckAuth,
						payload: {
							token: '',
							login: false,
							user: null
						}
					})
				}
				///////////////////////////////////////////////////////
				dispatch({ type: LoginFormActionTypes.Loading, payload: false })

			} catch (e) {
				dispatch({ type: LoginFormActionTypes.Loading, payload: false })
			}
		}
	}
	else {
		return (dispatch: Dispatch<LoginFormAction>) => {
			dispatch({ type: LoginFormActionTypes.Loading, payload: false })
		}
	}
}

export const Loading = (loading: boolean): LoginFormAction => {
	return {
		type: LoginFormActionTypes.Loading,
		payload: loading
	}
}

export const Logout = (): LoginFormAction => {
	localStorage.removeItem('token')
	return {
		type: LoginFormActionTypes.Logout
	}
}

export const Login = (state: ILoginFormState): LoginFormAction => {
	// отправлять на сервер тригер что это Login
	// принимаем email, pass, запомнить/нет
	// делаем запрос на сервер
	// через Axios.post(...url)
	// для проверки наличия пользователя
	// если все корректно возвращаем True (в App.tsx осуществляется переадресация)
	// от сервера получаем токен
	// есди данные не корректны возвращаем False
	let user
	let TestLogin
	let token

	///////////////////////////////////////////////////////
	if (state.email.value === 'инженер' && state.password.value === 'admin') {
		// при state.checkbox.chAction = true
		// сохраняем пользователя (его jwt полученный с сервера) в localStorage
		if (state.checkbox.chAction) {
			localStorage.setItem('token', 'инженер');
		}

		TestLogin = true
		user = {
			id: '1',
			name: 'инженер',
			email: 'инженер',
			phone: '+79111111111',
			rol: 'Я инженер',
			money: 1650,
			education: 'ЛЭТИ (2003-2008)',
			experience: 5,
			reputation: 4,
			transaction: false
		}
		token = 'инженер'
	}
	else if (state.email.value === 'подрядчик' && state.password.value === 'admin') {
		// при state.checkbox.chAction = true
		// сохраняем пользователя (его jwt полученный с сервера) в localStorage
		if (state.checkbox.chAction) {
			localStorage.setItem('token', 'подрядчик');
		}

		TestLogin = true
		user = {
			id: '1',
			name: 'подрядчик',
			email: 'подрядчик',
			phone: '+79111111111',
			rol: 'Я подрядчик',
			money: 1650,
			education: 'ЛЭТИ (2003-2008)',
			experience: 5,
			reputation: 4,
			transaction: false
		}
		token = 'подрядчик'
	}
	else if (state.email.value === 'заказчик' && state.password.value === 'admin') {
		// при state.checkbox.chAction = true
		// сохраняем пользователя (его jwt полученный с сервера) в localStorage
		if (state.checkbox.chAction) {
			localStorage.setItem('token', 'заказчик');
		}

		TestLogin = true
		user = {
			id: '1',
			name: 'заказчик',
			email: 'заказчик',
			phone: '+79111111111',
			rol: 'Я заказчик',
			money: 1650,
			education: 'ЛЭТИ (2003-2008)',
			experience: 5,
			reputation: 4,
			transaction: false
		}
		token = 'заказчик'
	}
	else {
		TestLogin = false
		user = null
		token = ''
	}
	///////////////////////////////////////////////////////

	let Texterror
	if (TestLogin) {
		Texterror = null
	}
	else {
		Texterror = 'Неправильный логин или пароль'
	}

	return {
		type: LoginFormActionTypes.Login,
		payload: {
			token: token,
			user: user,
			error: Texterror,
			login: TestLogin
		}
	}
}

export const Transaction = (money: string, card: string, email: string, token: string): LoginFormAction => {

	// получаем данные в строком формате
	// отправляем на сервер данные
	let TestTrans = {
		money: money,
		card: card,
		user: {
			email: email,
			token: token
		}
	}
	// с сервера получаем ответ
	let atServerTrans = {
		money: 1650 - (parseInt(money)),
		transaction: true
	}


	return {
		type: LoginFormActionTypes.Transaction,
		payload: {
			money: atServerTrans.money,
			transaction: atServerTrans.transaction
		}
	}
}


export const TransactionUpdate = (email: string, token: string): LoginFormAction => {

	// получаем данные в строком формате
	// отправляем на сервер данные пользователя для обновления данных по транзакциям
	let TestTrans = {
		user: {
			email: email,
			token: token
		}
	}
	// с сервера получаем ответ ( на сервере вычитаем из колонки балан сумму запроса и возвращаем разницу)
	let atServerTrans = {
		money: 1650,
		transaction: false
	}


	return {
		type: LoginFormActionTypes.Transaction,
		payload: {
			money: atServerTrans.money,
			transaction: atServerTrans.transaction
		}
	}
}