import React, { useState } from "react";
import "./engineer_money.css"
import exit_white from "../../../../img/exit_white.svg"
import exit_blue from "../../../../img/exit_blue.svg"
import { useDispatch, useSelector } from "react-redux";
import { TypeRedusers } from "../../../../store/reducers/redusers";
import { NavLink } from "react-router-dom";
import { bindActionCreators } from "redux";
import AllFunctions from "../../../../store/functions/Allfunctions"

const EngineerMoney: React.FC = () => {



	const dispatch = useDispatch()
	const { Transaction } = bindActionCreators(AllFunctions, dispatch)

	const { user, token } = useSelector((state: TypeRedusers) => state.login)

	const [card, setCard] = useState('')
	const [money, setMoney] = useState('')
	const [error, setError] = useState('')


	const isNumeric = (value: string) => {
		return /^\d+$/.test(value)
	}


	const getMoney = () => {
		if (parseInt(money) > user!.money || !card ||
			!money || card.length < 13) {
			setError('Данные введены некорректно')
		}
		else {
			Transaction(money, card, user!.email, token)
		}
	}

	if (user!.transaction) {
		return (
			<div className="engineer_money" >
				<div className="money_container_h2" >
					<h2>Запрос на перевод в обработке</h2>
					<NavLink to="/profile">
						<img src={exit_white} alt="" />
					</NavLink>
				</div>
			</div>
		)
	}

	return (
		<div className="engineer_money" >
			<div className="engineer_money_container" >
				<div className="engineer_money_exit">
					<NavLink to="/profile">
						<img src={exit_blue} alt="" />
					</NavLink>
				</div>
				<div className="engineer_money_title" >
					Вывод средств
				</div>
				<div className="engineer_money_sum" >
					Вам доступно: {user!.money} р
				</div>
				{console.log(111111111111)}
				<input
					value={card}
					onChange={(e) => {
						if (isNumeric(e.target.value) || e.target.value === '') {
							setCard(e.target.value)
						}
					}}
					className="card_number"
					placeholder="Введите номер карты"
					type="text" />


				<div className="all-in" >
					<input
						value={money}
						onChange={(e) => {
							if (isNumeric(e.target.value) || e.target.value === '') {
								setMoney(e.target.value)
							}
						}}

						placeholder="Введите сумму вывода"
						type="text" />
					<div
						onClick={() => {
							setMoney(`${user!.money}`)
						}}
					>
						Указать всю сумму
					</div>
				</div>
				<button
					onClick={getMoney} >
					Вывести
				</button>

				{
					error ?
						<div className="error" >
							{error}
						</div>
						:
						null
				}
			</div>
		</div>
	)
}

export default EngineerMoney