import { ISearchOrders, SearchOrdersAction, SearchOrdersActionTypes } from "../../types/searchOrders"


const initialState: ISearchOrders = {
	inputText: '',
	userChapters: [],
	days: 0,
	orders: [],
	title_text: 'Доступные проекты'
}


export const SearchOrdersReducer = (
	state = initialState,
	action: SearchOrdersAction): ISearchOrders => {
	switch (action.type) {
		case SearchOrdersActionTypes.OnChange_INPUT:
			return {
				...state,
				inputText: action.payload
			}

		case SearchOrdersActionTypes.OnChange_Days:
			return {
				...state,
				days: action.payload
			}

		case SearchOrdersActionTypes.OnChange_Chapters:
			return {
				...state,
				userChapters: action.payload
			}

		case SearchOrdersActionTypes.SearchOrders:
			return {
				...state,
				orders: action.payload.orders,
				title_text: action.payload.title_text
			}
		default:
			return state
	}
}