import { IRegFormState, RegFormAction, RegFormActionTypes } from "../../types/registration"





export const OnChangeName = (value: string): RegFormAction => {
	return {
		type: RegFormActionTypes.OnChange_NAME,
		payload: value
	}
}


const FrontСheckName = (name: string): string | null => {
	if (!name) {
		return 'Поле обязательно для ввода'
	}
	else {
		return null
	}
}

export const OnChangePass = (value: string): RegFormAction => {
	return {
		type: RegFormActionTypes.OnChange_PASS,
		payload: value
	}
}

const FrontСheckPass = (pass: string): string | null => {
	let regex =
		/^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)[a-zA-Z\d]{8,14}$/


	if (!pass) {
		return 'Поле обязательно для ввода'
	}
	else if (!regex.test(pass)) {
		return 'Пароля должен быть буквенно-цифровым с минимум одной заглавной буквой и содержать от 8 до 14 символов'
	}
	else {
		return null
	}
}


export const OnChangeEmail = (value: string): RegFormAction => {
	return {
		type: RegFormActionTypes.OnChange_EMAIL,
		payload: value
	}
}

const FrontСheckEmail = (email: string): string | null => {
	let regex = /^([a-z0-9_-]+\.)*[a-z0-9_-]+@[a-z0-9_-]+(\.[a-z0-9_-]+)*\.[a-z]{2,6}$/
	if (!email) {
		return 'Поле обязательно для ввода'
	}
	else if (!regex.test(email)) {
		return 'Введены некорректные данные'
	}
	else {
		return null
	}
}




export const OnChangePhone = (value: string): RegFormAction => {
	let tel

	if (value[0] !== '+') {
		tel = `+7${value}`
	}
	else if (value[1] === '7') {
		tel = value
	}
	else {
		tel = '+7'
	}

	return {
		type: RegFormActionTypes.OnChange_PHONE,
		payload: tel
	}
}

const FrontСheckPhone = (phone: string): string | null => {
	let regex = /^(\s*)?(\+)?([- _():=+]?\d[- _():=+]?){10,14}(\s*)?$/
	if (!phone) {
		return 'Поле обязательно для ввода'
	}
	else if (!regex.test(phone)) {
		return 'Введены некорректные данные'
	}
	else {
		return null
	}
}

export const OnClickRol = (rol: string, rols: string[]): RegFormAction => {
	let newArr = [rol]
	rols.map(r => {
		if (r !== rol) {
			newArr.push(r)
		}
	})
	return {
		type: RegFormActionTypes.OnClick_ROL,
		payload: newArr
	}
}

export const OnClickCheckbox = (value: boolean): RegFormAction => {
	return {
		type: RegFormActionTypes.OnClick_CHECKBOX,
		payload: value
	}
}


const FrontСheckCheckbox = (triger: boolean): string | null => {
	if (!triger) {
		return 'Для регистрации необходимо ваше согласие'
	}
	else {
		return null
	}
}


export const RegistrationSAVEorError = (state: IRegFormState): RegFormAction => {
	let nameError = FrontСheckName(state.name.value)
	let passError = FrontСheckPass(state.password.value)
	let emailErro = FrontСheckEmail(state.email.value)
	let phoneError = FrontСheckPhone(state.phone.value)
	let checkboxError = FrontСheckCheckbox(state.checkbox.chAction)

	let ErrForm = nameError || passError || emailErro || phoneError || checkboxError

	if (ErrForm) {
		return {
			type: RegFormActionTypes.SAVE_ERROR,
			payload: {
				nameError: nameError,
				passError: passError,
				emailError: emailErro,
				phoneError: phoneError,
				checkboxError: checkboxError
			}
		}
	}

	// добавляем тригер, что это регистрация
	// отправляем на сервер объект 
	let UserForServer = {
		name: state.name.value,
		password: state.password.value,
		email: state.email.value,
		phone: state.phone.value,
		rols: state.rols[0]
	}

	// в ответ получаем объект
	let UserForAtServer = {
		nameError: null, // или тект ошибки
		emailError: null,
		phoneError: null
	}

	ErrForm = UserForAtServer.emailError || UserForAtServer.nameError || UserForAtServer.phoneError

	if (ErrForm) {
		return {
			type: RegFormActionTypes.SAVE_ERROR,
			payload: {
				nameError: UserForAtServer.nameError,
				passError: passError,
				emailError: UserForAtServer.emailError,
				phoneError: UserForAtServer.phoneError,
				checkboxError: checkboxError
			}
		}
	}

	return {
		type: RegFormActionTypes.SAVE_CORRECT
	}

}


export const ResetRegistration = (): RegFormAction => {
	return {
		type: RegFormActionTypes.ResetRegistration
	}
}