import React from "react";
import "./engineer_search.css"

import exit_white from '../../../../img/exit_white.svg'

import EngineerOrder from "../engineer_order/engineer_order";
import { useDispatch, useSelector } from "react-redux";
import { TypeRedusers } from "../../../../store/reducers/redusers";
import { bindActionCreators } from "redux";
import SearchInput from "./search_input/search_input";
import AllFunctions from "../../../../store/functions/Allfunctions"
import FilterSmall from "./filter_small_container/filter_small";



const EngineerSearch: React.FC = () => {

	const dispatch = useDispatch()
	const { days, userChapters, title_text, orders } = useSelector((state: TypeRedusers) => state.searchOrders)
	const { SearchOnChangeChaptersRemove } = bindActionCreators(AllFunctions, dispatch)

	let num = `${days}`[`${days}`.length - 1]
	let text_day: string

	if (num === '1') {
		text_day = 'день'
	}
	else if (num === '2' || num === '3' || num === '4') {
		text_day = 'дня'
	}
	else {
		text_day = 'дней'
	}

	return (
		<div className="search" >
			<div className="search_header container" >
				<SearchInput />
				<div className="search_header_filter" >
					<div className="search_filter_text">
						{title_text}
					</div>
					<FilterSmall />
					<div className='search_filter_choice'>
						Срок: {days} {text_day}
					</div>
					{userChapters.map(chapter =>
						<div key={chapter} className='search_filter_choice'>
							Раздел: {chapter}
							<img
								onClick={() => {
									SearchOnChangeChaptersRemove(chapter, userChapters)
								}}
								src={exit_white} alt="" />
						</div>
					)}
				</div>
			</div>
			<EngineerOrder orders={orders} />
		</div>
	)
}

export default EngineerSearch