import React from "react";
import { NavLink, Route, useHistory } from "react-router-dom";
import { bindActionCreators } from "redux";
import logo1 from '../../img/logo1.svg'
import "./header.css"

import AllFunctions from "../../store/functions/Allfunctions"
import { useDispatch, useSelector } from "react-redux";
import { TypeRedusers } from "../../store/reducers/redusers";
import menu from '../../img/menu.svg'
const Header: React.FC = () => {

	const { login } = useSelector((state: TypeRedusers) => state.login)
	const dispatch = useDispatch()
	const { ResetRegistration } = bindActionCreators(AllFunctions, dispatch)



	return (
		<header>
			<div className="container header">

				{login ?
					<>
						<NavLink
							to='/profile'>
							<div
								className="header_logo">
								<img src={logo1} alt="" />
							</div>
						</NavLink>

						<NavLink
							to='/menu'>
							<div>
								<img
									className="menu_img"
									src={menu} alt="" />
							</div>
						</NavLink>
					</>
					:
					<>
						<NavLink
							onClick={ResetRegistration}
							to="/">
							<div
								className="header_logo">
								<img src={logo1} alt="" />
							</div>
						</NavLink>

						<NavLink to="/login">
							<div className="header_btn">
								<button>Войти</button>
							</div>
						</NavLink>
					</>
				}
			</div>
		</header>
	)
}

export default Header