import React from "react";
import { useDispatch, useSelector } from "react-redux";
import { bindActionCreators } from "redux";
import Checkbox from "../registration_form/checkbox/checkbox";
import InputEmail from "../registration_form/input_email/input_email";
import InputPassword from "../registration_form/input_password/input_password";
import TextError from "../registration_form/text_error/text_error";
import "./login_form.css"
import AllFunctions from "../../../../store/functions/Allfunctions"
import { TypeRedusers } from "../../../../store/reducers/redusers";

const LoginForm: React.FC = () => {

	const dispatch = useDispatch()

	const state = useSelector((state: TypeRedusers) => state.login)

	const { Login } = bindActionCreators(AllFunctions, dispatch)

	return (
		<div
			className="authorization_form">
			<div className="form_box">
				<div className="form_box_title">
					Вход в аккаунт
				</div>
				<div className="form_box_inputs">

					<InputEmail
						form='login'
						placeholder="Введите ваш e-mail" />
					<InputPassword
						form='login'
						placeholder="Введите ваш пароль" />
					<Checkbox
						form='login'
						label_text="Запомнить меня" />

					{state.error ? <TextError textError={state.error} /> : null}

					<button
						onClick={() => Login(state)}
						className="back_blue">
						Войти
					</button>

				</div>
			</div>
		</div>
	)
}

export default LoginForm