import React, { useEffect, useRef, useState } from "react";
import "./contractor_order.css"
import checkboxV from "../../../../img/V.svg"

const ContractorOrder: React.FC = () => {


	let chIng = false
	let chCus = true


	const ref_ing = useRef<HTMLDivElement>(null)
	const ref_cus = useRef<HTMLDivElement>(null)

	useEffect(() => {
		if (chIng) {
			ref_ing.current!.style.setProperty('--checkboxBC', '#0056d3')
		}
		if (chCus) {
			ref_cus.current!.style.setProperty('--checkboxBC', '#0056d3')
		}
	}, [chIng, chCus]);







	return (
		<div className="contractor_order">
			<div className="contractor_order_title" >
				<p>Заказ #1</p>
				<p>1600 ₽</p>
			</div>
			<div className="contractor_order_content" >
				<div className="contractor_order_text" >
					<p>1.	Стиль маркетплейса делаем как на лендинге</p>
					<p>2.	Для начала нужно сделать 3 страницы:</p>
					<p style={{ paddingLeft: "15px" }} >1.	Инженер</p>
					<p style={{ paddingLeft: "15px" }}>2.	Генеральный подрядчик или подрядчик</p>
					<p style={{ paddingLeft: "15px" }}>3.	Заказчик</p>

				</div>
				<div className="contractor_order_ch">
					<div
						ref={ref_ing}
						className="contractor_ch" >
						<div
							className="label">
							ПОДПИСЬ ИСПОЛНИТЕЛЯ
						</div>
						<div
							className="checkbox">
							<img
								src={checkboxV}
								alt="" />
						</div>
					</div>
					<div
						ref={ref_cus}
						className="contractor_ch">
						<div
							className="label">
							ПОДПИСЬ ЗАКАЗЧИКА
						</div>
						<div
							className="checkbox">
							<img
								src={checkboxV}
								alt="" />
						</div>

					</div>
				</div>
			</div>
			<div className="contractor_order_footer" >
				<button className="blue_btn" >Выбрать</button>
				<button className="white_btn" >Редактировать</button>
			</div>
		</div>
	)
}

export default ContractorOrder
