import React from "react";
import "./registration_form.css"
import InputName from "./input_name/input_name";
import InputPassword from "./input_password/input_password";
import InputEmail from "./input_email/input_email";
import InputPhone from "./input_phone/input_phone";
import Checkbox from "./checkbox/checkbox";
import Rols from "./rols/rols";
import { useDispatch, useSelector } from "react-redux";
import { TypeRedusers } from "../../../../store/reducers/redusers";
import AllFunctions from "../../../../store/functions/Allfunctions"
import { bindActionCreators } from "redux";
import { Redirect } from "react-router-dom";


const RegistrationForm: React.FC = () => {

	const dispatch = useDispatch()

	const state = useSelector((state: TypeRedusers) => state.registration)
	const { RegistrationSAVEorError } = bindActionCreators(AllFunctions, dispatch)


	if (!state.error) {
		return (
			<>
				<Redirect to="/login" />
			</>
		)
	}
	else {
		return (
			<div
				className="authorization_form">
				<div className="form_box">
					<div className="form_box_title">
						Регистрация
					</div>
					<div className="form_box_inputs">
						<InputName />
						<InputPassword
							form='registration'
							placeholder="Придумайте пароль" />
						<InputEmail
							form='registration'
							placeholder="Ваш e-mail" />
						<InputPhone />
						<Rols />
						<Checkbox
							form='registration'
							label_text="Я согласен на обработку персональных данных" />
						<button
							onClick={() => {
								RegistrationSAVEorError(state)
							}}
							className="back_blue">
							Зарегестрироваться
						</button>
					</div>
				</div>
			</div>
		)
	}
}


export default RegistrationForm