import { IRegFormState, RegFormAction, RegFormActionTypes } from "../../types/registration"

const initialState: IRegFormState = {
	name: {
		value: '',
		error: null
	},
	password: {
		value: '',
		error: null
	},
	email: {
		value: '',
		error: null
	},
	phone: {
		value: '',
		error: null
	},
	rols: [
		'Я инженер',
		'Я подрядчик',
		'Я заказчик'
	],
	checkbox: {
		chAction: false,
		error: null
	},
	error: true
}


export const RegistrationReduser = (
	state = initialState,
	action: RegFormAction): IRegFormState => {
	switch (action.type) {
		case RegFormActionTypes.OnChange_NAME:
			return {
				...state,
				name: {
					error: null,
					value: action.payload,
				}
			}

		case RegFormActionTypes.OnChange_PASS:
			return {
				...state,
				password: {
					error: null,
					value: action.payload,
				}
			}
		case RegFormActionTypes.OnChange_EMAIL:
			return {
				...state,
				email: {
					error: null,
					value: action.payload
				}
			}

		case RegFormActionTypes.OnChange_PHONE:
			return {
				...state,
				phone: {
					error: null,
					value: action.payload
				}
			}
		case RegFormActionTypes.OnClick_ROL:
			return {
				...state,
				rols: action.payload
			}
		case RegFormActionTypes.OnClick_CHECKBOX:
			return {
				...state,
				checkbox: {
					error: null,
					chAction: action.payload
				}
			}
		case RegFormActionTypes.SAVE_ERROR:
			return {
				...state,
				name: {
					...state.name,
					error: action.payload.nameError
				},
				password: {
					...state.password,
					error: action.payload.passError
				},
				email: {
					...state.email,
					error: action.payload.emailError
				},
				checkbox: {
					...state.checkbox,
					error: action.payload.checkboxError
				},
				phone: {
					...state.phone,
					error: action.payload.phoneError
				},
				error: true
			}
		case RegFormActionTypes.SAVE_CORRECT:
			return {
				name: {
					value: '',
					error: null
				},
				password: {
					value: '',
					error: null
				},
				email: {
					value: '',
					error: null
				},
				phone: {
					value: '',
					error: null
				},
				rols: [
					'Я инженер',
					'Я подрядчик',
					'Я заказчик'
				],
				checkbox: {
					chAction: false,
					error: null
				},
				error: false
			}

		case RegFormActionTypes.ResetRegistration:
			return {
				...state,
				error: true
			}

		default:
			return state
	}
}