import { ILoginFormState, LoginFormAction, LoginFormActionTypes } from "../../types/login"

const initialState: ILoginFormState = {
	email: {
		value: '',
		error: null
	},
	password: {
		value: '',
		error: null
	},
	checkbox: {
		chAction: false,
		error: null
	},
	login: false,
	error: null,
	user: null,
	loading: true,
	token: ''
}

export const LoginReducer = (
	state = initialState,
	action: LoginFormAction): ILoginFormState => {
	switch (action.type) {
		case LoginFormActionTypes.OnChange_EMAIL:
			return {
				...state,
				email: {
					error: null,
					value: action.payload
				}
			}

		case LoginFormActionTypes.OnChange_PASS:
			return {
				...state,
				password: {
					error: null,
					value: action.payload
				}
			}

		case LoginFormActionTypes.OnClick_CHECKBOX:
			return {
				...state,
				checkbox: {
					chAction: action.payload,
					error: null
				}
			}
		case LoginFormActionTypes.Login:
			return {
				...state,
				login: action.payload.login,
				error: action.payload.error,
				user: action.payload.user,
				token: action.payload.token
			}
		case LoginFormActionTypes.CheckAuth:
			return {
				...state,
				login: action.payload.login,
				user: action.payload.user,
				token: action.payload.token
			}
		case LoginFormActionTypes.Logout:
			return {
				...state,
				email: {
					value: '',
					error: null
				},
				password: {
					value: '',
					error: null
				},
				checkbox: {
					chAction: false,
					error: null
				},
				login: false,
				error: null,
				user: null,
				token: ''
			}
		case LoginFormActionTypes.Loading:
			return {
				...state,
				loading: action.payload
			}

		case LoginFormActionTypes.Transaction:
			return {
				...state,
				user: {
					...state.user!,
					money: action.payload.money,
					transaction: action.payload.transaction
				}
			}
		default:
			return state
	}
}