import { EngineerOrdersAction, EnginerOrdersActionTypes, IEnginerOrder } from "../../types/engineerOrders"


export const EnginerAgreedOrders = (
	userId: string,
	userName: string,
	userRol: string,
	userToken: string
): EngineerOrdersAction => {

	// на сервер направляем данные пользователя и строку Agreed - Согласованные

	// в ответ получаем 
	let Orders: IEnginerOrder[] = [
		{
			days: 1,
			details: 'Полное описание заказа',
			group: 'СОУЭ',
			hot: true,
			id: 'ААА111111',
			ready: 50,
			title: 'Поверка'
		}
	]

	return {
		type: EnginerOrdersActionTypes.EnginerOrders,
		payload: Orders
	}
}

