interface IinputState {
	value: string
	error: string | null
}


interface IcheckboxState {
	chAction: boolean
	error: string | null
}


export interface IRegFormState {
	name: IinputState
	password: IinputState
	email: IinputState
	phone: IinputState
	rols: string[]
	checkbox: IcheckboxState
	error: boolean
}



export enum RegFormActionTypes {
	OnChange_NAME = "OnChange_NAME",
	OnChange_PASS = "OnChange_PASS",
	OnChange_EMAIL = "OnChange_EMAIL",
	OnChange_PHONE = "OnChange_PHONE",
	OnChange_Error = "OnChange_Error",
	OnClick_ROL = "OnClick_ROL",
	OnClick_CHECKBOX = "OnClick_Checkbox",
	SAVE_ERROR = "SAVE_ERROR",
	SAVE_CORRECT = "SAVE_CORRECT",
	ResetRegistration = "ResetRegistration"
}


interface I_OnChange_NAME_Action {
	type: RegFormActionTypes.OnChange_NAME
	payload: string
}

interface I_OnChange_PASS_Action {
	type: RegFormActionTypes.OnChange_PASS
	payload: string
}

interface I_OnChange_EMAIL_Action {
	type: RegFormActionTypes.OnChange_EMAIL
	payload: string
}

interface I_OnChange_PHONE_Action {
	type: RegFormActionTypes.OnChange_PHONE
	payload: string
}

interface I_OnChange_Error_Action {
	type: RegFormActionTypes.OnChange_Error
}

interface I_OnClick_ROL_Action {
	type: RegFormActionTypes.OnClick_ROL
	payload: string[]
}

interface I_OnClick_CHECKBOX_Action {
	type: RegFormActionTypes.OnClick_CHECKBOX
	payload: boolean
}



interface I_SAVE_ERROR_Action {
	type: RegFormActionTypes.SAVE_ERROR
	payload: {
		nameError: string | null,
		passError: string | null,
		emailError: string | null,
		phoneError: string | null,
		checkboxError: string | null,
	}
}

interface I_SAVE_CORRECT_Action {
	type: RegFormActionTypes.SAVE_CORRECT
}


interface I_ResetRegistration_Action {
	type: RegFormActionTypes.ResetRegistration
}


export type RegFormAction =
	I_OnChange_NAME_Action |
	I_OnChange_PASS_Action |
	I_OnChange_EMAIL_Action |
	I_OnChange_PHONE_Action |
	I_OnClick_ROL_Action |
	I_OnClick_CHECKBOX_Action | I_SAVE_ERROR_Action |
	I_OnChange_Error_Action | I_SAVE_CORRECT_Action |
	I_ResetRegistration_Action




